import { mapState } from 'vuex'
export default {
	data(){
		return{
			backgroundColor:'#000',
		}
	},
	created() {
		
	},
	computed:{
		...mapState({
			userInfo: state => state.user.userInfo
		})
	},
	methods: {
		back(){
			uni.navigateBack()
		},
		chrole(role){
			console.log(this.userInfo)
			this.confirm().then(()=>{
				let userInfo = this.userInfo
				if(userInfo.role){
					uni.showToast({
					    title: '身份不允许更改',
						icon: 'none',
						position: 'bottom',
					    duration: 500
					});
					return
				}
				userInfo.role = role
				delete userInfo.meta
				delete userInfo.__v
				this.$store.dispatch('chrole',userInfo)
				if(role == 1){
					uni.navigateTo({
						url: `../setting/setting`
					})
				}
				else{
					uni.showToast({
					    title: '暂未开通',
						icon: 'none',
						position: 'bottom',
					    duration: 500
					});
				}
			})
		},
		confirm(){
			return new Promise((resolve,reject)=>{
				uni.showModal({
				    title: '提示',
				    content: '身份选定后不能更改',
				    success: function (res) {
				        if (res.confirm) {
				            resolve()
				        } else if (res.cancel) {
				            return
				        }
				    }
				});
			})
		},
			
	}
}
